/**
* MODAL
*/

.modal_wrapper{
      width: 100%;
      height: 100vh;
      position: fixed;
      background: rgba(0,0,0,.5);
      opacity: 0;
      visibility: hidden;
      align-items: center;
      justify-content: center;
      z-index: 5;
      top: 0;
      left: 0;
      transition: .3s ease-in-out;
}

.modal_wrapper.active{
      opacity: 1;
      visibility: visible;
}

.modal_inner{
      width: fit-content;
      max-width: 80%;
      height: fit-content;
      max-height: 80vh;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.modal_inner img{
      width: fit-content;
}

.btn_close__modal{
      width: 5rem;
      height: 5rem;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 0;
      right: 0;
      background: var(--primary);
}

.btn_close__modal *{
      color: var(--light);
      font-size: 2.4rem;
}