/**
* SOCIAL 
*/

.aside_left__social ul{
      margin-top: 2rem;
}

.social_link__item{
      padding-left: 1rem;
}

.social_link__item a{
      display: flex;
      width: 100%;
      height: 3.6rem;
      align-items: center;
      gap: 1rem;
      border-radius: .4rem;
      transition: .2s;
      text-transform: capitalize;
}

.social_link__item a:hover{
      background: var(--bg-300);
      padding: 0 1rem;
}

.social_link__item a i{
      color: var(--text-300);
      transition: .2s;
}

.social_link__item a i *{
      font-size: 1.8rem;
}

.social_link__item a span{
      color: var(--text-200);
      transition: .2s;
}

.social_link__item.facebook a:hover i{
      color: var(--facebook);
}

.social_link__item.twitter a:hover i{
      color: var(--twitter);
}

.social_link__item.instagram a:hover i{
      color: var(--instagram);
}

.social_link__item.youtube a:hover i{
      color: var(--youtube);
}

.social_link__item.tumblr a:hover i{
      color: var(--tumblr);
}

.social_link__item.discord a:hover i{
      color: var(--discord);
}

.social_link__item.tiktok a:hover i{
      color: var(--tiktok);
}

.social_link__item.twitch a:hover i{
      color: var(--twitch);
}

.social_link__item a:hover > span{
      color: var(--text-100);
}