/**
* HOME
*/

.home_page{
      width: 100%;
      height: auto;
      border-radius: 1rem;
      background: linear-gradient(90deg, var(--bg-300) 25rem, var(--bg-200) 25rem);
      border: solid .1rem var(--border);
      position: relative;
}

.home_page__banner{
      width: 100%;
      height: 20rem;
      border-radius: 1rem 1rem 0 0;
      position: absolute;
      top: .1rem;
      left: .1rem;
}

.home_page__banner:before{
      width: 100%;
      height: 100%;
      toP: 0;
      left: 0;
      background: rgba(77, 77, 77, 0.3);
      border-radius: 1rem 1rem 0 0;
      -webkit-backdrop-filter: blur(30px);
      backdrop-filter: blur(30px);
}

.home_page__front{
      position: relative;
      width: 100%;
      height: auto;
      min-height: 42rem;
}

.home_front__user_info{
      width: 25rem;
      height: auto;
      padding: 2rem;
      flex-direction: column;
      align-items: center;
      flex-shrink: 0;
}

.home_front__user_info h2{
      font-size: 2.8rem;
      margin-top: 2rem;
}

.front_user__avatar{
      width: 18.4rem;
      height: 18.4rem;
      border-radius: 50%;
      overflow: hidden;
      background: var(--bg-100);
      box-shadow: 0 0 0 .3rem var(--primary);
      margin-top: 3rem;
}

.front_user__info{
      margin-top: 4rem;
      width: 100%;
}

.user_info__col{
      flex-direction: column;
      align-items: center;
      justify-content: center;
}

.user_info__col strong{
      text-transform: uppercase;
      font-size: 1.4rem;
      font-weight: 500;
}

.user_info__col span{
      color: var(--primary);
      font-size: 1.2rem;
      font-weight: 500;
      margin-top: .5rem;
}

.home_front__user_data{
      position: relative;
      padding: 2rem;
      flex: 1;
      height: auto;
}

.front_user__data_nav{
      width: 100%;
      height: auto;
      flex-direction: column;
      align-items: flex-end;
      gap: 1rem;
}

.front_user__data_nav a{
      height: 3.8rem;
      background: var(--primary);
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      color: var(--light);
      padding: 0 2.5rem;
      gap: 1rem;
      border-radius: 999px;
      font-size: 1.4rem;
}

.front_user__data_nav a *{
      color: var(--light);
}

.front_user__data_location{
      margin-top: 5rem;
      position: relative;
      gap: 1rem;
      align-items: center;
      transform: translateX(-3.5rem);
}

.front_user__data_location *{
      color: var(--primary);
      font-size: 2.5rem;
}

.front_user__data_location span{
      color: var(--light);
      font-size: 1.8rem;
      font-weight: 500;
}

.front_user__data_description{
      margin-top: 3rem;
      color: var(--text-200);
}

.front_user__data_description p{
      font-family: var(--ft-2);
      line-height: 2.2rem;
      white-space: pre-line;
}

 /* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 880px) {
      .front_user__data_location span{
            font-size: 1.4rem;
      }
}