/**
* EMPTY
*/

.empty_page{
      padding: 5rem;
      flex-direction: column;
      align-items: center;
      color: var(--text-300);
}

.empty_page h1{
      font-size: 3.2rem;
}

.empty_page h2{
      font-size: 2rem;
}

.empty_icon{
      width: 20rem;
      height: 20rem;
      align-items: center;
      justify-content: center;
      background: var(--bg-300);
      border: solid .1rem var(--border);
      border-radius: 50%;
      margin: 2rem 0;
}

.empty_icon *{
      font-size: 10rem;
}

.empty_description p{
      text-align: center;
      font-family: var(--ft-2);
      line-height: 2.2rem;
}