/**
* MOBILE FRIENDS
*/

.mobile_content{
      width: 11rem;
      flex-shrink: 0;
      height: auto;
      padding: 1rem;
      display: none;
}

.mobile_content__header{
      width: 100%;
      height: 8rem;
      align-items: center;
}

.mobile_header__box{
      width: 5rem;
      height: 5rem;
      position: relative;
      background: var(--bg-300);
      border-radius: 50%;
}

.mobile_header__icon{
      width: 5rem;
      height: 5rem;
      align-items: center;
      justify-content: center;
      color: var(--primary);
}

.mobile_header__icon *{
      font-size: 2rem;
}

.header_box__count{
      width: 2rem;
      height: 2rem;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 0;
      right: 0;
      background: var(--primary);
      color: var(--light);
      border-radius: 50%;
      font-size: 1.2rem;
}

.mobile_content__list{
      margin-top: 0;
}

.mobile_friend__row{
      width: 100%;
      height: 8rem;
      align-items: center;
      gap: 1rem;
      position: relative;
}

.mobile_friend__row_avatar{
      width: 6rem;
      height: 6rem;
      position: relative;
      background: var(--bg-300);
      border-radius: 50%;
}

.mobile_row__avatar_img{
      width: 8rem;
      height: 8rem;
      position: absolute;
      background-color: var(--bg-300);
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(.68);
      border-radius: 50%;
}

.mobile_friend__btn{
      width: .8rem;
      height: 3.8rem;
      flex-direction: column;
      justify-content: space-between;
      background: none;
}

.mobile_friend__btn span{
      width: .8rem;
      height: .8rem;
      background: var(--bg-200);
      border: solid .2rem var(--border);
      border-radius: 50%;
}

.mobile_friend__row_data{
      position: absolute;
      left: 1rem;
      top: 0;
      width: 100%;
      height: 8rem;
      padding: 1rem;
      background: var(--bg-300);
      border: solid .1rem var(--border);
      border-radius: .4rem;
      opacity: 0;
      visibility: hidden;
      transition: .2s ease-in-out;
}

.mobile_friend__row_data.active{
      opacity: 1;
      visibility: visible;
      left: 0;
}

.mobile_friend__row_data span{
      width: 100%;
      text-align: center;
      font-weight: 500;
      font-size: 1.2rem;
      line-height: 2.6rem;
      display: block;
}

.mobile_friend__row_data a{
      width: 100%;
      line-height: 2.4rem;
      border-radius: .3rem;
      background: var(--primary);
      text-transform: uppercase;
      color: var(--light);
      text-aligN: center;
      font-size: 1.2rem;
}

.mobile_friend__row_data button{
      width: 2rem;
      height: 2rem;
      position: absolute;
      toP: 0;
      right: 0;
      align-items: center;
      justify-content: center;
      background: var(--primary);
      border-radius: 50%;
      transform: translate(50%, -50%);
}

.mobile_friend__row_data button *{
      color: var(--light);
      font-size: 1rem;
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (max-width: 1080px) {
      .mobile_content{
          display: block;
      }
} 