/**
* VIDEO ASIDE
*/

.aside_left__video{
      width: 24.2rem;
      height: auto;
      margin-top: var(--gap-section);
}

.aside_video__box{
      width: 24.2rem;
      height: 13.7rem;
      background: var(--bg-200);
      border-radius: 1rem;
      overflow: hidden;
}

.aside_video__box iframe{
      border: none;
      outline: none;
}

.aside_left__video span{
      margin-top: 1rem;
      color: var(--text-300);
      font-weight: 500;
}

.aside_left__video p{
      font-size: 1.4rem;
      text-transform: capitalize;
}