/**
* PHOTOS
*/

.images_wrapper{
      width: 100%;
      height: auto;
      columns: 2;
      column-gap: 1rem;
}

.card_image{
      position: relative;
      width: 100%;
      height: fit-content;
      border-radius: .6rem;
      overflow: hidden;
      margin-bottom: .7rem;
}

.card_image__img{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border-radius: .6rem;
      overflow: hidden;
      align-items: center;
      justify-content: center;
}

.card_image__img img{
      border-radius: .6rem;
      transform: scale(1);
      transition: .3s ease-in-out;
}

.card_image:hover .card_image__img img{
      transform: scale(1.3);
}

.card_image__front{
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, rgba(0,0,0,.6));
      align-items: flex-end;
      overflow: hidden;
      padding: 1rem;
      opacity: 0;
      transition: .3s ease-in-out;
}

.card_image:hover .card_image__front{
      opacity: 1;
}

.card_image__front p{
      width: calc(100% - 7rem);
      height: auto;
      text-transform: capitalize;
      font-family: var(--ft-2);
}

.card_image__front button{
      width: 4rem;
      height: 4rem;
      background: rgba(0,0,0,.3);
      border-radius: 50%;
      position: absolute;
      right: 1rem;
      bottom: 1rem;
      display: flex;
      align-items:center;
      justify-content: center;
      transform: translateY(200%);
      transition: .3s ease-in-out;
}

.card_image:hover .card_image__front button{
      transform: translateY(0);
}

.card_image__front button *{
      color: var(--primary);
      font-size: 2rem;
}