@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&family=Rajdhani:wght@300;400;500;600;700&family=Roboto+Condensed:wght@200;400;500;600&family=Rubik:wght@300;400;500;600;700;800&family=Saira+Condensed:wght@200;300;400;500;600&display=swap');

:root{
    /** fonts */
    --ft-1: 'Rubik', sans-serif;
    --ft-2: 'Saira Condensed', sans-serif;
    --ft-3: 'Montserrat', sans-serif;
    --ft-4: 'Roboto Condensed', sans-serif;

    /** theme */
    --primary: #4165fe;
    
    /** colors */
    --bg-300: #0c1115;
    --bg-200: #16191c;
    --bg-100: #1c2025;
    --border: #24272b;
    --text-300: #5c5e62;
    --text-200: #c4c4c4;
    --text-100: #ebebeb;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;
    --yellow: #ffbf34;

    /** spacing */
    --gap-section: 2rem;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #1da1f2;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-1);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-300);
    color: var(--text-100);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-1);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    widtH: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 120rem;
    min-width: 54rem;
    height: auto;
    position: relative;
    margin: 0 auto;
}

.base{
    border-radius: 1rem;
    background: var(--bg-200);
    border: solid .1rem var(--border);
    
}
 
 /* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 768px) {
    .aside.left{
        display: none;
    }
}
 
 /* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (max-width: 1080px) {
    .aside_right{
        display: none;
    }

    .mobile_content{
        display: block;
    }
} 