/**
* SHARED
*/

.shared_content{
      position: relative;
      height: 4rem;
}

.shared_nav{
      position: absolute;
      height: 4rem;
      width: fit-content;
      align-items: center;
      gap: .75rem;
      top: 0;
      left: 0;
      transform: translate(-110%, -5rem);
      background: var(--bg-300);
      border-radius: 999px;
      padding: 0 .5rem;
      opacity: 0;
      visibility: hidden;
      transition: .2s ease-in-out;
}

.shared_nav.show{
      opacity: 1;
      visibility: visible;
      transform: translate(-110%, 0);
}

.share_link{
      width: 3.2rem;
      height: 3.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--bg-200);
      border-radius: 50%;
      transition: .2s ease-in-out;
}

.share_link:hover{
      background: var(--bg-100);
}

.share_link *{
      font-size: 1.8rem;
}

.share_link.facebook{
      color: var(--facebook);
}

.share_link.twitter{
      color: var(--twitter);
}

.share_link.pinterest{
      color: var(--pinterest);
}

.share_link.linkedin{
      color: var(--linkedin);
}

.share_link.whatsapp{
      color: var(--whatsapp);
}

.shared_button{
      height: 4rem;
      align-items: center;
      padding: 0 1rem;
      background: none;
      gap: .5rem;
}

.shared_button *{
      font-size: 2rem;
      color: var(--primary);
}

.shared_button span{
      color: var(--text-300);
      font-size: 1.4rem;
}