/**
* POSTS
*/

.posts_wrapper{
      width: 100%;
      height: auto;
      flex-direction: column;
      gap: 3rem;
}

.post_card{
      width: 100%;
      height: auto;
      background: var(--bg-200);
      border-radius: 1rem;
      border: solid .1rem var(--border);
}

.post_card_top{
      width: 100%;
      height: 31rem;
      position: relative;
}

.post_card__top_cover{
      width: 100%;
      height: 31rem;
      align-items: center;
      justify-content: center;
      border-radius: 1rem 1rem 0 0;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
}

.post_card__top_cover img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.post_card__top_title{
      width: 100%;
      height: 100%;
      position: relative;
      padding: 1rem;
      background: linear-gradient(transparent, var(--bg-200));
      align-items: flex-end;
}

.post_card__top_title h1{
      position: relative;
      padding-left: 2rem;
      font-size: 1.8rem;
      color: var(--ligth);
      font-weight: 600;
}

.post_card__top_title h1:before{
      width: 1rem;
      height: 1rem;
      background: var(--primary);
      top: .3rem;
      left: 0;
      border-radius: 50%;
}

.post_card__middle{
      padding: 2rem 1.5rem;
      border-top: solid .1rem var(--border);
}

.post_card__middle p{
      white-space: pre-line;
      color: var(--text-200);
      font-family: var(--ft-2);
      line-height: 2.2rem;
}

.post_card__bottom{
      padding: 1rem 2rem;
      border-top: solid .1rem var(--border);
      align-items: center;
      justify-content: space-between;
}

.post_card__publish span{
      color: var(--text-300);
      font-size: 1.2rem;
      font-style: italic;
}