/**
* ONLINE FRIEND
*/

.online_friend{
      position: absolute;
      width: 2rem;
      height: 2rem;
      background: var(--bg-100);
      border-radius: 50%;
      bottom: 0;
      right: 0;
      border: solid .3rem var(--bg-200);
      padding: .2rem;
}

.online_friend__dot{
      width: 100%;
      height: 100%;
      background: var(--bg-300);
      border-radius: 50%; 
}

.online_friend__dot.yellow{
      background: var(--yellow);
}

.online_friend__dot.red{
      background: var(--red);
}

.online_friend__dot.green{
      background: var(--green);
}