/**
* RELATION
*/

.relation_wrapper{
      width: 100%;
      height: auto;
      padding: 1.5rem;
      background: var(--bg-200);
      border-radius: 1rem;
      border: solid .1rem var(--border);
}

.relation_header{
      padding: 1rem 0;
      align-items: center;
      gap: 1rem;
}

.relation_header *{
      font-size: 2rem;
      color: var(--primary);
}

.relation_header span{
      text-transform: uppercase;
      color: var(--text-100);
      font-size: 1.6rem;
}

.relation_card{
      margin-top: 1rem;
      width: 100%;
      height: auto;
}

.relation_top{
      width: 100%;
      height: 35rem;
      border-radius: 1rem;
      position: relative;
      overflow: hidden;
}

.relation_top__image{
      width: 70%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
}

.relation_top__front{
      position: relative;
      width: 100%;
      height: 100%;
}

.relation_top__front:before{
      width: 100%;
      height: 100%;
      background: var(--primary);
      clip-path: polygon(0 0, 70% 0%, 100% 100%, 0% 100%);
      transform: translateX(-35%);
}

.relation_front__data{
      position: relative;
      width: 100%;
      height: 100%;
      padding: 2rem;
      flex-direction: column;
      justify-content: flex-end;
}

.relation_front__data h3{
      font-size: 3.4rem;
      color: var(--bg-300);
}

.relation_front__data strong{

}

.relation_front__data p{
      font-weight: 500;
      font-size: 1.4rem;
}

.relation_data__nav{
      margin-top: 2rem;
      gap: 2rem;
}

.relation_data__nav a{
      width: fit-content;
      height: 3.8rem;
      line-height: 3.4rem;
      text-transform: uppercase;
      color: var(--light);
      padding: 0 2rem;
      border: solid .2rem var(--light);
      border-radius: 999px;
      font-size: 1.2rem;
      letter-spacing: .1rem;
      font-weight: 500;
      transition: .2s ease-in-out;
}

.relation_data__nav a:hover{
      background: var(--light);
      color: var(--primary);
      border: solid .2rem var(--primary);
}

.relation_middle{
      margin-top: 2rem;
      padding-bottom: 2rem;
}

.relation_middle p{
      white-space: pre-line;
      color: var(--text-200);
      font-family: var(--ft-2);
      line-height: 2.2rem;
}