/**
* MAIN
*/

.main_content{
      flex: 1;
      height: auto;
}

.banner_pager{
      width: 100%;
      height: 25rem;
      background: var(--bg-200);
      border-radius: 1rem;
      position: relative;
}

.banner_pager:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border-radius: 1rem;
      background: linear-gradient(225deg, rgba(0,0,0,0), rgba(0,0,0,.5));
      z-index: 2;
}

.banner_pager__img{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      border-radius: 1rem;
      overflow: hidden;
}

.banner_pager__img img{
      min-height: 100%;
}

.menu_pager{
      width: 100%;
      height: 7rem;
      margin-top: 6rem;
}

.navigate_pages{
      width: 100%;
      height: 100%;
      padding: 1rem 2rem;
      border-radius: .6rem;
      align-items: center;
      justify-content: space-between;
}

.navigate_button{
      flex-direction: column;
      align-items: center;
      position: relative;
      height: 6rem;
      background: none;
}

.navigate_button__icon{
      width: 6rem;
      height: 6rem;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--primary);
      transition: .2s ease-in-out;
}

.navigate_button__icon *{
      font-size: 3rem;
}

.navigate_button.active .navigate_button__icon{
      background: var(--primary);
      flex-shrink: 0;
      color: var(--light);
      box-shadow: 0 0 10px var(--primary);
      transform: translateY(-3.5rem)
}

.navigate_button:hover .navigate_button__icon{
      color: var(--light);
}

.navigate_button__title{
      position: absolute;
      text-transform: uppercase;
      color: var(--primary);
      bottom: -1rem;
      left: 50%;
      transform: translate(-50%, 20%);
      font-size: 1.4rem;
      opacity: 0;
      transition: .2s ease-in-out;
}

.navigate_button.active .navigate_button__title{
      opacity: 1;
      transform: translate(-50%, -100%);
}