/**
* VIDEOS
*/

.videos_wrapper{
      padding: 1.5rem;
}

.videos_header{
      padding: 1rem 0;
      align-items: center;
      gap: 1rem;
}

.videos_header *{
      color: var(--primary);
      font-size: 2rem;
}

.videos_header span{
      text-transform: uppercase;
      font-size: 1.6rem;
      color: var(--text-100);
}

.videos_content{
      width: 100%;
      height: auto;
      margin-top: 1rem;
}

.video_row{
      width: 100%;
      height: auto;
      margin-bottom: 3rem;
}

.video_row__box{
      width: 100%;
      height: 30.4rem;
      background: var(--bg-300);
      border-radius: 1rem;
      overflow: hidden;
}

.video_row__box iframe{
      border: none;
      outline: none;
}

.video_row__title{
      height: 4rem;
      width: 100%;
      margin-top: 1rem;
      align-items: center;
}

.video_row__title h3{
      position: relative;
      padding: 1rem 1rem 1rem 3rem;
      font-weight: 500;
      width: 100%;
      height: auto;
}

.video_row__title h3:before{
      width: 1rem;
      height: 1rem;
      background: var(--primary);
      border-radius: 50%;
      left: 1rem;
      top: 1.25rem;
}