/**
* ASIDELEFT
*/

.aside_left{
      width: 24.2rem;
      height: auto;
      padding: 2rem;
      flex-shrink: 0;
}

.aside_left__links span{
      color: var(--text-300);
      font-weight: 500;
}

.aside_left__links ul{
      margin-top: 1rem;
      padding-left: 1rem;
      width: 100%;
}

.aside_left__links ul li{
      margin-bottom: .2rem; 
}

.aside_left__links ul li a{
      display: flex;
      align-items: center;
      gap: 1rem;
      height: 3.6rem;
      color: var(--text-200);
      border-radius: .4rem;
      padding: 0 0;
      text-transform: capitalize;
      font-size: 1.6rem;
      transition: .2s;
}

.aside_left__links ul li a *{
      color: var(--text-300);
      font-size: 1.8rem;
}

.aside_left__links ul li a:hover{
      background: var(--bg-300);
      color: var(--text-100);
      padding: 0 1rem;
}

.aside_left__links ul li a:hover *{
      color: var(--primary);
}

.aside_left__social{
      margin-top: 1rem;
      border-top: solid .1rem var(--border);
      padding-top: 1.5rem;
}

.aside_left__social > span{
      color: var(--text-300);
      font-weight: 500;
}