/**
* USER
*/

.card_user{
      width: 100%;
      height: 44.5rem;
      background: var(--bg-200);
      border: solid .1rem var(--border);
      border-radius: 1rem;
      position: relative;
}

.card_user__front{
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
}

.card_user__front_banner{
      width: 100%;
      height: 15rem;
      position: absolute;
      align-items: center;
      justify-content: center;
      border-radius: 1rem 1rem 0 0;
      overflow: hidden;
}

.card_user__front_banner img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.card_user__front_banner:before{
      width: 100%;
      height: 100%;
      background: rgba(202, 202, 202, 0.5);
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);
      top: 0;
      left: 0;
      z-index: 1;
}

.card_user__front_banner:after{
      width: calc(100% + .1rem);
      height: 7rem;
      background: var(--bg-200);
      left: 0;
      bottom: 0;
      clip-path: polygon(50% 100%, 100% 0, 100% 100%, 0 100%, 0 0);
      z-index: 2;
}

.card_user__front_data{
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      position: relative;
      z-index: 3;
      padding-top: 7rem;
}

.user_data__avatar{
      width: 16rem;
      height: 16rem;
      background: var(--bg-200);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      position: relative;
}

.user_data__avatar:before{
      width: 15rem;
      height: 15rem;
      background: var(--bg-200);
      border: solid .2rem var(--primary);
      border-radius: 50%;
      box-sizing: border-box;
      top: .5rem;
      left: .5rem;
      border-right: solid .2rem transparent;
      transform: rotate(-45deg);
}

.user_data__avatar_img{
      width: 14rem;
      height: 14rem;
      border-radius: 50%;
      overflow: hidden;
      position: relative;
      align-items: center;
      justify-content: center;
}

.user_data__avatar_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_data__info{
      flex-direction: column;
      margin-top: 1.5rem;
      align-items: center;
}

.user_data__info h2{
      font-size: 2rem;
      font-weight: 500;
}

.user_data__info span{
      color: var(--text-300);
}

.user_data__info strong{
      color: var(--primary);
      font-weight: 500;
}

.user_data__amount{
      margin-top: 2rem;
      width: 100%;
      padding: 0 1rem;
}

.amount_grid{
      gap: .5rem;
      width: 100%;
}

.amount_col{
      flex-direction: column;
      align-items: center;
}

.amount_col span{
      text-transform: uppercase;
      color: var(--text-300);
      font-family: var(--ft-2);
      font-weight: 500;
      font-size: 1.4rem;
}

.amount_col strong{
      font-size: 2rem;
}

.amount_col__flag{
      width: 3rem;
}

.user_data__about{
      position: absolute;
      width: 100%;
      height: 38rem;
      background: var(--bg-100);
      border-radius: .6rem;
      padding: 5rem 2rem 2rem 2rem;
      top: calc(100% - 5rem);
      transition: .5s ease-in-out;
}

.user_data__about.active{
      top: 7rem;
}

.user_data__about h4{
      color: var(--primary);
      font-size: 1.8rem;
      font-weight: 500;
}

.about_description{
      margin-top: 1.5rem;
}

.about_description p{
      font-family: var(--ft-2);
      color: var(--text-200);
      line-height: 2.2rem;
}

.user_read{
      width: 14rem;
      height: 4.6rem;
      align-items: center;
      position: absolute;
      left: 50%;
      bottom: 0;
      z-index: 4;
      transform: translate(-50%, 50%);
      justify-content: center;
      gap: 1rem;
      border-radius: 999px;
      background: var(--primary);
}

.user_read span{
      color: var(--light);
}

.read_icon{
      width: 3.2rem;
      height: 3.2rem;
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.1);
      border-radius: 50%;
      transition: .3s ease-in-out;
      transform: rotate(180deg);
}

.user_read.active .read_icon{
      transform: rotate(0);
}

.read_icon *{
      color: var(--light);
}