/**
* ONLINE 
*/

.online_user{
      position: absolute;
      align-items: center;
      width: fit-content;
      gap: 1rem;
      height: 2.8rem;
      bottom: .75rem;
      left: 11rem;
}

.online_dots{
      width: 2.8rem;
      height: 2.8rem;
      background: var(--bg-100);
      border: solid .4rem var(--bg-200);
      border-radius: 50%;
      padding: .3rem;
      position: relative;
}

.online_dots:before{
      width: 1.4rem;
      height: 1.4rem;
      background: var(--bg-200);
      border-radius: 50%;
      top: calc(50% - .7rem);
      left: calc(50% - .7rem);
}

.online_dots.yellow:before{
      background: var(--yellow);
}

.online_dots.red:before{
      background: var(--red);
}

.online_dots.green:before{
      background: var(--green);
}

.online_user span{
      font-weight: 500;
      font-size: 1.4rem;
}